import fb from "../firebaseConfig"


const settingsCollection = fb.db.collection('settings')

async function getOrderItemsByOrderId(orderId) {
    return orderCollection.doc(orderId).collection("orderItems").get().then(ss => {
        if (ss.empty == false) return ss.docs.map(doc => doc.data());
        else return [];
    });
}

export default {
    getSettings: async function (orderId) {
        var settings = {};
        await settingsCollection.limit(1).get().then(ss => settings = ss.docs[0].data());
        return settings;
    },
    setSettings: async function (settings) {
        settings.dateModified = new Date();
        await settingsCollection.doc(settings.id).set(settings);
        return settings;
    }

}
