<template>
  <div class="animated fadeIn">
    <!-- Product batches -->
    <!-- <b-row>
      <b-col>
        <textarea v-model="productBatches" cols="30" rows="10" class="form-control"></textarea>
        <b-button type="submit" size="sm" variant="success" @click="saveProductBatches">
          <i class="fa fa-save"></i> Save
        </b-button>
      </b-col>
    </b-row>-->
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-book"></i>
              Products ({{productCount}})
            </span>
            <div class="pull-right">
              <b-link @click="createSupplierPrices">Create Supplier Prices |</b-link>
              <b-link @click="clearAllSalePrices">Clear Sale Prices |</b-link>
              <b-link @click="addNewProduct" class="ml-1">Add New Product</b-link>
            </div>
          </div>
          <b-row>
            <b-col sm="12" md="6">
              <b-input-group class="mb-5">
                <b-form-input v-model="searchText" @keydown="seartTextKeydown($event)"></b-form-input>

                <b-input-group-append>
                  <span
                    id="searchclear"
                    class="fa fa-close"
                    v-if="searchText != ''"
                    @click="clearSearchText"
                  ></span>
                  <b-button variant="outline-success" @click="clickSearchProduct()">
                    <span class="fa fa-search"></span> Search
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
          <view-port>
            <template v-slot:mobile>
              <products-list-mobile
                :products="products"
                :search-text="searchText"
                @productClicked="productClicked"
                @deleteProduct="deleteProduct"
                @editProduct="editProduct"
              ></products-list-mobile>
            </template>
            <template v-slot:desktop>
              <products-list
                :products="products"
                :search-text="searchText"
                @productClicked="productClicked"
                @deleteProduct="deleteProduct"
                @editProduct="editProduct"
              ></products-list>
            </template>
          </view-port>

          <b-row class="load-more">
            <b-button
              variant="outline-success"
              @click="loadMoreProduct($event)"
              v-loading="isLoadingMore"
            >Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->

    <product-detail ref="productDetail" @productSaved="productSaved" />
  </div>
</template>

<script>
import fb from "firebase";
import productDetail from "./productDetail";
import productsList from "./productsList";
import productsListMobile from "./productsListMobile";
import { generateSubString } from "@/shared/utils";
import viewPort from "../shared/viewPort";

export default {
  name: "Products",
  components: {
    productDetail,
    productsList,
    productsListMobile,
    viewPort
  },
  data() {
    return {
      products: [],
      product: {},
      productBatches: "",
      useMobileView: false,
      searchText: "",
      pageSize: 10,
      lastVisible: undefined,
      productCount: 0,
      isLoadingMore: false,
      saleProductCount: 0,
      saleProducts: []
    };
  },
  mounted: function() {
    var self = this;
    self.useMobileView = window.innerWidth < 700;
    window.addEventListener("resize", function() {
      self.useMobileView = window.innerWidth < 700;
    });
    this.loadProducts();
    this.$fb.counterService.getProductCount().then(count => (this.productCount = count));
  },
  watch: {
    searchText: function(newValue, oldValue) {
      if (oldValue != "" && newValue == "") {
        this.products = [];
        this.loadProducts();
      }
    }
  },
  computed: {},
  methods: {
    loadProducts: function(lastVisible) {
      this.isLoadingMore = true;
      return this.$fb.productCollection
        .orderBy("name")
        .startAfter(this.lastVisible || 0)
        .limit(this.pageSize)
        .get()
        .then(snapshot => {
          this.isLoadingMore = false;
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
          }
        });
    },
    loadMoreProduct: function(e) {
      this.isLoadingMore = true;
      if (this.searchText) {
        this.searchProduct(this.lastVisible).then(() => (this.isLoadingMore = false));
      } else {
        this.loadProducts(this.lastVisible).then(() => (this.isLoadingMore = false));
      }
    },
    productClicked: function(product) {
      this.product = { ...product };
    },
    deleteProduct: function(data) {
      var product = data.item;
      var index = data.index;

      if (!product) return;

      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete [${product.name}]?`)
        .then(value => {
          if (value) {
            this.$fb.productCollection
              .doc(product.id)
              .delete()
              .then(() => {
                this.products.splice(index, 1);
              });
            this.$fb.counterService.decreaseProduct().then(x => (this.productCount = x));
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    addNewProduct: function() {
      this.product = undefined;
      this.selectedIndex = undefined;
      this.$refs.productDetail.open(undefined);
    },
    productSaved: function(prod) {
      if (this.selectedIndex != undefined) {
        //edit
        this.products.splice(this.selectedIndex, 1, prod);
      } else {
        this.products.push(prod);
      }
    },
    editProduct: function(data) {
      var { index, item } = data;
      this.product = { ...item };
      this.selectedIndex = index;
      this.$refs.productDetail.open(this.product);
    },
    saveProductBatches: function() {
      var productBatches = JSON.parse(this.productBatches);

      productBatches.forEach(prod => {
        prod.nameSearch = generateSubString(prod.name);
      });

      console.table(productBatches);
      // Get a new write batch
      var batch = this.$fb.db.batch();

      productBatches.forEach(prod => {
        var newProd = this.$fb.productCollection.doc();
        prod.id = newProd.id;
        batch.set(newProd, prod);
      });

      // Commit the batch
      batch.commit().then(function() {
        console.log("Finish Product Batches");
      });
    },
    clickSearchProduct: function() {
      this.products = [];
      this.searchProduct();
    },
    searchProduct: function(lastVisible) {
      if (!this.searchText) return;

      return this.$fb.productCollection
        .where("nameSearch", "array-contains", this.searchText.toLowerCase().trim())
        .orderBy("name")
        .startAfter(lastVisible || 0)
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
          }
        });
    },
    seartTextKeydown: function(e) {
      if (e.keyCode === 13) {
        this.products = [];
        this.searchProduct();
      }
    },
    clearSearchText: function() {
      this.searchText = "";
    },
    checkCwPrice: function() {},
    clearAllSalePrices: function() {
      this.$fb.productCollection
        .where("cwSalePricePick", "==", true)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.saleProductCount = snapshot.docs.length;
            if (this.saleProductCount > 0) {
              snapshot.docs.forEach(doc => this.saleProducts.push(doc.data()));
              this.$bvModal
                .msgBoxConfirm(
                  `Are you sure you want to remove sale price for [${this.saleProductCount}] products?`
                )
                .then(value => {
                  if (value) {
                    this.saleProducts.forEach(item => {
                      item.cwSalePricePick = false;
                      this.$fb.productCollection.doc(item.id).set(item);
                    });
                  }
                })
                .catch(err => {
                  console.log(err);
                });
            }
          }
        });
    },
    createSupplierPrices: async function() {
      var prods = [];
      await this.$fb.productCollection
        .orderBy("name")
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            snapshot.forEach(doc => {
              prods.push(doc.data());
            });
          }
        });
      console.log(prods);

      prods.forEach(x => {
        console.log("create supplier price for:", x.name);
        x.supplierPrices = [];
        if (x.cwPrice) {
          x.supplierPrices.push({
            price: x.cwPrice,
            supplier: "CW",
            salePrice: x.cwSalePrice || "",
            salePricePick: x.cwSalePricePick || false
          });
        }

        if (x.bvPrice) {
          x.supplierPrices.push({
            price: x.bvPrice,
            supplier: "BV",
            salePrice: x.bvSalePrice || "",
            salePricePick: x.bvSalePricePick || false
          });
        }

        this.$fb.productCollection.doc(x.id).set(x);
      });
    }
  }
};
</script>
<style>
#searchclear {
  position: absolute;
  right: 90px;
  top: 0;
  bottom: 0;
  height: 14px;
  margin: auto;
  font-size: 14px;
  cursor: pointer;
  color: #ccc;
  z-index: 1019;
}
</style>
