<template>
  <b-modal
    :id="intId"
    :title="'Product Detail - ' + (prod.id || '')"
    @ok="saveProduct"
    size="lg"
    modal-class="product-detail-modal"
  >
    <cw-product-predictive @productMatched="updateCwMatchedProduct" ref="cwProductPredictive"></cw-product-predictive>
    <b-row>
      <b-col sm="8">
        <b-form-group>
          <label for="name">Name</label>
          <b-input-group>
            <b-form-input type="text" placeholder="Enter product name" v-model="prod.name"></b-form-input>
            <b-input-group-append>
              <b-button variant="outline-info" @click="checkCwMatchedProduct">Check</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
        <b-form-group>
          <label for="name">Thumbnail Url</label>
          <b-form-input type="text" placeholder="Enter product image url" v-model="prod.thumbnail"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <div class="card">
          <img
            :src="prod.thumbnail"
            :alt="prod.name"
            width="150px"
            height="150px"
            style="margin: auto"
          />
        </div>
      </b-col>
    </b-row>
    <template v-for="(sp, index) in prod.supplierPrices">
      <b-form-group :key="index">
        <b-row>
          <b-col sm="3">
            <b-form-select v-model="sp.supplier" :options="suppliers"></b-form-select>
          </b-col>
          <b-col sm="3">
            <b-form-input type="text" placeholder="Price (A$)" v-model="sp.price"></b-form-input>
          </b-col>
          <b-col sm="3">
            <b-form-input type="text" placeholder="Sale Price (A$)" v-model="sp.salePrice"></b-form-input>
          </b-col>
          <b-col sm="2" style="padding-top: 5px">
            <b-form-checkbox
              v-model="sp.salePricePick"
              @change="(checked) => salePricePickChanged(index, checked)"
            >Apply sale price</b-form-checkbox>
          </b-col>
          <b-col sm="1">
            <b-link @click="removeSupplierPrice(index)">
              <i
                class="fa fa-times text-danger fa-lg"
                style="padding-top: 13px;"
                aria-hidden="true"
              ></i>
            </b-link>
          </b-col>
        </b-row>
      </b-form-group>
    </template>
    <b-form-group>
      <b-link @click="addSuppplierPrice">Add Supplier Price</b-link>
    </b-form-group>
    <b-row>
      <b-col sm="2">
        <b-form-group>
          <label for="weight">Weight (g)</label>
          <b-link class="ml-2" @click="searchShipppingWeight">Search</b-link>
          <b-form-input type="text" id="weight" placeholder="0" v-model="prod.weight"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="2">
        <b-form-group>
          <label for="shipprice">Shipment Price (A$)</label>
          <b-form-input
            type="text"
            id="shipprice"
            placeholder="0.00"
            v-model="prod.shipPrice"
            :disabled="true"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="selprice">Selling Price (A$)</label>
          <b-form-input
            type="text"
            id="selprice"
            placeholder="0.00"
            v-model="prod.selPrice"
            :disabled="true"
          ></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="4">
        <b-form-group>
          <label for="buyprice">Buying Price (A$)</label>
          <b-form-input
            type="text"
            id="buyprice"
            placeholder="0.00"
            v-model="prod.buyPrice"
            :disabled="true"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group description="Buy. Price + Ship">
          <label for="oriprice">Ori. Price (A$)</label> -
          <vnd-price :value="prod.oriPrice"></vnd-price>
          <b-form-input
            type="text"
            id="oriprice"
            placeholder="0.00"
            v-model="prod.oriPrice"
            :disabled="true"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group description="Sel. Price + Commission(10%) + Ship + $2">
          <label for="sugprice">
            Sug. Price (A$) -
            <vnd-price :value="prod.sugPrice"></vnd-price>
          </label>
          <b-form-input placeholder="0.00" v-model="prod.sugPrice" :disabled="true"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="rndprice">
            Rnd. Price (VND) -
            <i style="color: red">{{prod.rndPrice | currency(vndConfig)}}</i>
          </label>
          <b-form-input type="text" placeholder="0.00" v-model="prod.rndPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import fb from "firebase";
import appSettings from "@/shared/appSettings";
const settings = appSettings.priceSettings;
import vndPrice from "../shared/vndPrice";
import { generateSubString } from "@/shared/utils";
import cwProductPredictive from "./cwProductPredictive";
import DatePick from "../shared/vueDatePick";

export default {
  name: "ProductDetail",
  components: { vndPrice, cwProductPredictive, DatePick },
  props: ["id", "readonly"],
  data() {
    return {
      prod: {},
      settings: settings,
      cwMatchedProduct: {},
      cwMatchedProductChange: false,
      isCheckingCwPrice: false,
      settings: {}
    };
  },
  mounted: function() {
    this.$fbServices.settingService.getSettings().then(s => (this.settings = s));
  },
  watch: {
    "prod.supplierPrices": {
      deep: true,
      handler: function(value) {
        this.calculatePrices(value);
      }
    },
    "prod.weight": function(value) {
      if (value && parseFloat(value) > 0) {
        this.prod.shipPrice = ((parseFloat(value) * this.settings.shipPricePerKg) / 100).roundTo(2);
      }
    }
  },
  computed: {
    intId: function() {
      return this.id || "productdetail";
    },
    suppliers: function() {
      if (!this.settings || !this.settings.suppliers || this.settings.suppliers.length == 0)
        return [];
      return this.settings.suppliers.map(x => {
        return { text: x.name, value: x.code };
      });
    }
  },
  methods: {
    open: function(product) {
      if (!product) this.prod = {};
      else {
        this.prod = { ...product };
        this.prod.supplierPrices = [];
        if (product.supplierPrices && product.supplierPrices.length > 0) {
          product.supplierPrices.forEach(x => this.prod.supplierPrices.push(x));
        }
      }

      this.$bvModal.show(this.intId);
      this.$nextTick(
        () => this.prod.cwProductId && this.$refs.cwProductPredictive.checkPrice(this.prod)
      );
    },
    saveProduct: function() {
      var prod = this.prod;
      prod.nameSearch = generateSubString(prod.name);
      if (!prod.name) return;

      if (this.readonly) {
        this.$emit("productSaved", { ...prod });
        this.$bvModal.hide(this.intId);
        return;
      }

      if (prod.id) {
        return this.$fb.productCollection
          .doc(prod.id)
          .set(prod)
          .then(() => {
            this.$emit("productSaved", { ...prod });
            this.$bvModal.hide(this.intId);
            return prod;
          });
      } else {
        var newProd = this.$fb.productCollection.doc();
        prod.id = newProd.id;
        return newProd.set(prod).then(() => {
          this.$fb.counterService.increaseProduct();
          this.$emit("productSaved", { ...prod });
          this.$bvModal.hide(this.intId);
          return prod;
        });
      }
    },
    calculatePrices: function(supplierPrices) {
      var { weight } = this.prod;
      this.prod.shipPrice = (
        (parseFloat(weight || 0) * this.settings.shipPricePerKg) /
        100
      ).roundTo(2);
      if (!supplierPrices || supplierPrices.length == 0) return;
      this.prod.selPrice = _.maxBy(supplierPrices, sp => parseFloat(sp.price || 0)).price;
      this.prod.buyPrice = _.minBy(supplierPrices, sp => parseFloat(sp.price || 9999)).price;

      this.calculateItself();

      // //override if using sale price

      var salePricePickSup = this.prod.supplierPrices.find(x => x.salePricePick);
      if (salePricePickSup) {
        this.prod.selPrice = salePricePickSup.salePrice;
        this.prod.buyPrice = salePricePickSup.salePrice;

        this.calculateItself();
      }
    },
    calculateItself: function() {
      this.prod.oriPrice = (
        parseFloat(this.prod.shipPrice) + parseFloat(this.prod.buyPrice)
      ).toFixed(2);
      this.prod.sugPrice = parseFloat(
        this.prod.shipPrice +
          this.prod.selPrice * this.settings.commissionRate +
          this.settings.buyingFee
      ).toFixed(2);
      this.prod.sugPriceVND = parseFloat(this.prod.sugPrice * this.settings.vndExchageRate).toFixed(
        2
      );
    },

    salePricePickChanged: function(index, checked) {
      console.log("sale price pick change");
      if (checked) {
        //reset all salePricePick to false
        this.prod.supplierPrices.forEach((x, i) => {
          x.salePricePick = false;
          this.prod.supplierPrices.splice(i, 1, x);
        });
      }

      var sp = this.prod.supplierPrices[index];
      sp.salePricePick = checked;
      this.prod.supplierPrices.splice(index, 1, sp);
    },
    updateCwMatchedProduct: function(item) {
      var { thumbnail, id, priceStatus, price, label } = item;

      this.prod.name = label;

      if (!this.prod.thumbnail) this.$set(this.prod, "thumbnail", thumbnail);
      else if (thumbnail && thumbnail != this.prod.thumbnail) {
        this.prod.thumbnail = thumbnail;
      }

      if (!this.prod.cwProductId) this.$set(this.prod, "cwProductId", id);
      else if (this.prod.cwProductId != id) {
        this.prod.cwProductId = id;
      }

      if (!this.prod.supplierPrices) this.$set(this.prod, "supplierPrices", new Array());
      var cwPriceIndex = this.prod.supplierPrices.findIndex(x => x.supplier == "CW");
      if (cwPriceIndex == -1) {
        this.prod.supplierPrices.push({
          price: price,
          salePrice: "",
          salePricePick: false
        });
      } else {
        var cwPrice = this.prod.supplierPrices[cwPriceIndex];
        cwPrice.price = price;
        this.prod.supplierPrices.splice(cwPriceIndex, 1, cwPrice);
      }
    },
    checkCwMatchedProduct: function() {
      this.$refs.cwProductPredictive.checkPrice(this.prod, true);
    },
    searchShipppingWeight: function() {
      var url =
        "https://www.google.com.au/search?source=hp&ei=b54BXsjALoiW4-EPl8izwA4&q={term}&oq={term}&gs_l=psy-ab.3...9077.14394..15665...1.0..0.238.3323.0j15j3......0....2j1..gws-wiz.......33i21j33i10.9gohnIlLrZE&ved=0ahUKEwiItLOew83mAhUIyzgGHRfkDOgQ4dUDCAg&uact=5";
      var term = this.prod.name + " " + "shipping weight".replace(" ", "+");
      url = url.replace("{term}", term);
      var win = window.open(url, "_blank");
      win.focus();
    },
    addSuppplierPrice: function() {
      if (!this.prod.supplierPrices) this.$set(this.prod, "supplierPrices", new Array());
      this.prod.supplierPrices.push({
        supplier: {},
        price: "",
        salePrice: ""
      });
    },
    removeSupplierPrice: function(index) {
      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete this supplier price ?`)
        .then(async value => {
          if (value) {
            this.prod.supplierPrices.splice(index, 1);
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style scoped>
</style>