<template>
  <div>
    <view-port>
      <template v-slot:desktop>
        <template v-for="(sp, index) in product.supplierPrices">
          <span class="price-gutter" v-if="index > 0">
            <br v-if="!!breakline" />
            <span v-if="!breakline">&nbsp;&nbsp;</span>
          </span>

          <span :key="index">
            <span v-if="!sp.salePricePick">
              <strong>{{sp.supplier}}:</strong>
              {{sp.price | currency(audConfig)}}
            </span>

            <span v-if="sp.salePricePick">
              <strong>{{sp.supplier}}:</strong>
              <del>{{sp.price | currency(audConfig)}}</del>
              <span>{{sp.salePrice | currency(audConfig)}}</span>
              <b-badge variant="success" style="margin-left: 3px;">Sale</b-badge>
            </span>
          </span>
        </template>
        <span v-if="!product.supplierPrices"></span>
      </template>
      <template v-slot:mobile>
        <span v-if="!product.supplierPrices"></span>
        <template v-for="(sp, index) in product.supplierPrices">
          <div :key="index">
            <span v-if="!sp.salePricePick">
              <strong>{{sp.supplier}}:</strong>
              {{sp.price | currency(audConfig)}}
            </span>
            <span v-if="sp.salePricePick">
              <strong>{{sp.supplier}}:</strong>
              <del>{{sp.price | currency(audConfig)}}</del>
              <span>{{sp.salePrice | currency(audConfig)}}</span>
              <b-badge variant="success" style="margin-left: 3px;">Sale</b-badge>
            </span>
          </div>
        </template>
      </template>
    </view-port>
  </div>
</template>

<script>
import viewPort from "../shared/viewPort";
export default {
  name: "ShowSupplierPrices",
  components: { viewPort },
  props: ["product", "breakline"],
  data() {
    return {};
  },
  mounted: function() {},
  watch: {},
  computed: {},
  methods: {}
};
</script>
<style scoped>
.sp-item:not(:first-child) {
  margin-left: 10px;
}
</style>