import fb from "../firebaseConfig"


const collection = fb.db.collection('distribution')
var setDebounceFunc = null;
async function getItem(id) {
    return collection.doc(id).get().then(ss => ss.data());
}

async function getOrderItemsByOrderId(orderId) {
    return orderCollection.doc(orderId).collection("orderItems").get().then(ss => {
        if (ss.empty == false) return ss.docs.map(doc => doc.data());
        else return [];
    });
}

function buildItem(inventoryItem, order, orderItem) {
    return {
        orderId: order.id,
        orderItemId: orderItem.id,
        quantity:
            inventoryItem.quantity > orderItem.quantity
                ? orderItem.quantity
                : inventoryItem.quantity,
        inventoryId: inventoryItem.id,
        date: new Date()
    };
}

export default {
    buildItem: buildItem,
    set: async function (inventoryId, order, orderItem) {
        var item = buildItem(inventoryId, order, orderItem);

        var ref = collection.doc();
        item.id = ref.id;
        await ref.set(item);

        return item;
    },
    delete: async function (id) {
        await collection.doc(id).delete();
        return true;
    }
}
