function buildAuditItem(item, message, distribution) {
    return {
        date: new Date(),
        message: message,
        quantity: item.quantity,
        distributionId: (distribution && distribution.id) || "",
        product: {
            id: item.product.id,
            name: item.product.name,
            price: item.product.rndPrice
        }
    }
}

export default {
    buildAuditItem: buildAuditItem,
    set: async function (item, distribution) {
        if (!item.audit) item.audit = [];
        if (item.id) {
            item.audit.push(buildAuditItem(item, "Update", distribution));
            await collection
                .doc(item.id)
                .set(item);
        }
        else {
            item.audit.push(buildAuditItem(item, "Init"));
            var ref = collection.doc();
            item.id = ref.id;
            await ref.set(item);
        }
        return item;
    },
    get: async function (id) {
        return await collection.doc(id).get().then(x => x.data());
    }
}
