<template>
  <b-modal :id="modalId" :title="modalTitle" size="lg">
    <b-table
      ref="inventoryTable"
      responsive="sm"
      :items="audit"
      :fields="fields"
      :small="true"
      selectable
      select-mode="single"
    >
      <template v-slot:cell(index)="data">{{data.index + 1}}</template>
      <template v-slot:cell(date)="data">{{data.value.toDate() | datetime}}</template>
      <template v-slot:cell(distribution)="data">
        <div
          v-if="data.item.distributionId"
        >{{"distribute: [{0}] to order: {1} - order iten: {2}".format(data.item.distribution.quantity, data.item.distribution.orderId, data.item.distribution.orderItemId)}}</div>
      </template>
    </b-table>
  </b-modal>
</template>

<script>
export default {
  name: "InventoryItemHistory",
  props: ["id"],
  components: {},
  data() {
    return {
      item: {},
      audit: [],
      modalTitle: "",
      modalId: "",
      fields: [
        { key: "index", label: "#", class: "i-index" },
        { key: "message", label: "Event", class: "ih-event" },
        { key: "date", label: "Date", class: "ih-date" },
        { key: "product.price", label: "Price", class: "ih-price" },
        { key: "quantity", label: "Quantity", class: "ih-quantity" },
        { key: "distribution", class: "ih-distribution" }
      ]
    };
  },
  mounted: function() {
    this.modalId = this.id || "inventoryEditItemModal";
  },
  watch: {},
  computed: {},
  methods: {
    open: async function(item) {
      this.item = await this.$fbServices.inventoryService.get(item.id);
      var { name, rndPrice } = this.item.product;
      var { quantity } = this.item;
      this.modalTitle = "Inventory: {0} - Price: {1} - Qty: {2}".format(name, rndPrice, quantity);
      this.audit = _.orderBy(this.item.audit, x => x.date, "desc");
      this.audit.forEach(x => {
        if (x.distributionId) {
          x.distribution = this.item.distributions.find(y => (y.id = x.distributionId));
        }
      });
      this.$nextTick(() => this.$bvModal.show(this.modalId));
    }
  }
};
</script>
