import fb from "../firebaseConfig"


const orderCollection = fb.db.collection('orders')

async function getOrderById(orderId) {
    return orderCollection.doc(orderId).get().then(ss => ss.data());
}

async function getOrderItemsByOrderId(orderId) {
    return orderCollection.doc(orderId).collection("orderItems").get().then(ss => {
        if (ss.empty == false) return ss.docs.map(doc => doc.data());
        else return [];
    });
}

export default {
    getOrderAndItemsByOrderId: async function (orderId) {
        var order = await getOrderById(orderId);
        var items = await getOrderItemsByOrderId(orderId);
        return { order: order, orderItems: items };
    },
    setOrderItem: async function (orderId, orderItem) {
        if (orderItem.id) { //update
            orderItem.dateModified = new Date();
            await orderCollection.doc(orderId).collection("orderItems").doc(orderItem.id).set(orderItem);
        }
        else { //insert
            orderItem.dateCreated = new Date();
            var ref = orderCollection.doc(orderId).collection("orderItems").doc();
            orderItem.id = ref.id;
            await ref.set(orderItem);
        }
        return orderItem;
    },
    deleteOrderItem: function (orderId, orderItemId) {
        return orderCollection.doc(orderId).collection("orderItems").doc(orderItemId).delete();
    },
    getLast10Orders: async function () {
        return orderCollection
            .limit(10)
            .orderBy("dateCreated", "desc")
            .get()
            .then(ss => {
                if (ss.empty) return [];
                else return ss.docs.map(d => d.data());
            })
    }
}
