import Vue from 'vue'

Vue.directive('loading', {
    bind: function (el, binding, vNode) {
    },
    inserted: function (el, binding) {
    },
    update: function (el, binding) {
        var spinner = `<div class="spinner-border spinner-border-sm text-success ml-1" role="status"></div>`;
        if (binding.value) {
            if (el.innerHTML.indexOf(spinner) == -1) {
                el.innerHTML = el.innerHTML + spinner;
                el.disabled = true;
            }
        }
        else {
            el.innerHTML = el.innerHTML.replace(spinner, "");
            el.disabled = false;
        }
    }
})