<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <b-link @click="goToOrderList">
              <i class="fa fa-server"></i> Order:
            </b-link>
            <strong>{{order.name}}</strong>
            <div class="pull-right">
              <b-link @click="goToOrderDetailPage">Order Detail Page</b-link>
            </div>
          </div>
          <view-port>
            <template v-slot:desktop>
              <div>
                <b-table
                  foot-clone
                  responsive="sm"
                  :items="items"
                  :fields="fields"
                  :small="true"
                  :hover="true"
                  selectable
                  select-mode="single"
                >
                  <template v-slot:cell(index)="data">{{data.index +1}}</template>
                  <template
                    v-slot:cell(total)="data"
                  >{{calculateTotalOrderItem(data.item) | currency(vndConfig)}}</template>
                  <template v-slot:cell(name)="data">
                    {{data.item.name}}
                    <b-link
                      :href="appSettings.cwBuyProductUrl.replace('{id}', data.item.cwProductId)"
                      target="_blank"
                      v-if="data.item.cwProductId"
                    >[Buy CW]</b-link>
                    <br />
                    <show-supplier-prices :product="data.item.product"></show-supplier-prices>
                  </template>
                  <template v-slot:cell(collectedQuantity)="data">
                    <number-input
                      v-model="data.item.collectedQuantity"
                      :min="0"
                      :max="data.item.quantity"
                      inline
                      controls
                      center
                      size="sm"
                      @change="collectedQuantityChanged(data.item, $event)"
                    ></number-input>
                  </template>
                  <template v-slot:cell(collected)="data">
                    <b-form-checkbox
                      v-model="data.item.collected"
                      @change="collectedChanged($event, data.item)"
                    ></b-form-checkbox>
                  </template>
                  <template v-slot:cell(action)="data">
                    <b-button type="submit" size="sm" variant="success" class="mr-1">
                      <i class="fa fa-edit"></i> Edit
                    </b-button>
                    <b-button
                      type="submit"
                      size="sm"
                      variant="danger"
                      @click="deleteOrderItem(data.item)"
                    >
                      <i class="fa fa-remove"></i> Delete
                    </b-button>
                  </template>

                  <!-- footer -->
                  <template v-slot:foot()="data"></template>
                  <template v-slot:foot(quantity)="data">{{sumQuantity}}</template>
                  <template v-slot:foot(collectedQuantity)="data">{{sumCollectedQuantity}}</template>
                </b-table>
              </div>
            </template>
            <template v-slot:mobile>
              <b-table
                foot-clone
                responsive="sm"
                :items="items"
                :fields="mobileFields"
                :small="true"
              >
                <template v-slot:cell(index)="data">
                  <b-row>
                    <b-col>
                      <strong>{{data.index +1}}. {{data.item.name}}</strong>
                      <b-link
                        class="ml-1"
                        :href="appSettings.cwBuyProductUrl.replace('{id}', data.item.cwProductId)"
                        target="_blank"
                        v-if="data.item.cwProductId"
                      >[Buy CW]</b-link>
                      <br />
                      <show-supplier-prices :product="data.item.product"></show-supplier-prices>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col>
                      Quantity:
                      <strong>{{data.item.quantity}}</strong>
                      ({{data.item.customerNameQty}})
                    </b-col>
                  </b-row>
                  <b-row class="mt-2">
                    <b-col cols="6">
                      <number-input
                        v-model="data.item.collectedQuantity"
                        :min="0"
                        :max="data.item.quantity"
                        inline
                        controls
                        center
                        size="sm"
                        @change="collectedQuantityChanged(data.item, $event)"
                      ></number-input>
                    </b-col>
                    <b-col cols="6" class="text-center" style="padding-top: 5px">
                      <b-form-checkbox
                        v-model="data.item.collected"
                        @change="collectedChanged($event, data.item)"
                      ></b-form-checkbox>
                    </b-col>
                  </b-row>
                </template>

                <!-- footer -->
                <template v-slot:foot()="data">
                  <hr />
                  <b-row>
                    <b-col>Quantity</b-col>
                    <b-col class="text-right">{{sumQuantity}}</b-col>
                  </b-row>
                  <b-row>
                    <b-col>Collected Qty</b-col>
                    <b-col class="text-right">{{sumCollectedQuantity}}</b-col>
                  </b-row>
                </template>
              </b-table>
            </template>
          </view-port>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
  </div>
</template>

<script>
import _ from "lodash";
import numberInput from "../shared/numberInput";
import viewPort from "../shared/viewPort";
import ShowSupplierPrices from "./ShowSupplierPrices";
var saveOrderTimeoutFunc;
export default {
  name: "OrderDetail",
  components: { numberInput, viewPort, ShowSupplierPrices },
  data() {
    return {
      order: {},
      items: [],
      fields: [
        { key: "index", label: "#", class: "collection-index" },
        { key: "name", label: "Product", class: "collection-name" },
        { key: "customerNameQty", label: "Detail", class: "collection-detail" },
        {
          key: "quantity",
          label: "Quantity",
          class: "text-right collection-qty"
        },
        {
          key: "collectedQuantity",
          label: "Collected Qty",
          class: "text-center collection-collected-qty"
        },
        {
          key: "collected",
          label: "Collected",
          class: "text-center collection-collected"
        }
      ],
      mobileFields: [{ key: "index", label: "" }]
    };
  },
  mounted: function() {
    this.loadOrder();
  },
  computed: {
    sumQuantity: function() {
      if (!this.items || this.items.length === 0) return 0;
      return _.sumBy(this.items, x => x.quantity);
    },
    sumCollectedQuantity: function() {
      if (!this.items || this.items.length === 0) return 0;
      return _.sumBy(this.items, x => x.collectedQuantity);
    }
  },
  methods: {
    loadOrder: function() {
      this.$fb.orderCollection.doc(this.$route.params.id).onSnapshot(snapshot => {
        if (!snapshot.empty) {
          this.order = snapshot.data();
          this.items = _.chain(this.order.items)
            .map(x => {
              return {
                id: x.id,
                name: x.product.name,
                cwProductId: x.product.cwProductId,
                quantity: x.quantity,
                collectedQuantity: x.collectedQuantity,
                product: x.product,
                collected: x.collected,
                customerName: x.customer.name
              };
            })
            .groupBy(x => x.name)
            .mapValues(val => {
              var re = {
                name: _.first(val).name,
                product: _.first(val).product,
                cwProductId: _.first(val).cwProductId,
                quantity: _.sumBy(val, x => parseFloat(x.quantity)),
                collectedQuantity: _.sumBy(val, x => parseFloat(x.collectedQuantity)),
                orderItemIds: _.reduce(val, (s, x) => s.concat(x.id), []),
                customerNameQty: _.map(val, item => item.customerName + ": " + item.quantity).join(
                  ", "
                )
              };
              re.collected = re.quantity == re.collectedQuantity;

              return re;
            })
            .values()
            .orderBy("name")
            .value();
        }
      });
    },
    goToOrderDetailPage: function() {
      this.$router.push({ name: "OrderDetail", params: { id: this.order.id } });
    },
    collectedChanged: function(checked, item) {
      if (checked) {
        item.collectedQuantity = item.quantity;
      } else {
        item.collectedQuantity = 0;
      }
      this.setCollectedItem(item);
    },
    setCollectedItem: function(item) {
      var { orderItemIds } = item;
      var collectedQuantity = item.collectedQuantity;
      this.order.items
        .filter(x => orderItemIds.indexOf(x.id) > -1)
        .forEach(x => {
          x.collectedQuantity = 0;
          x.collected = false;
          if (collectedQuantity > 0) {
            if (collectedQuantity > x.quantity) {
              x.collectedQuantity = x.quantity;
              x.collected = true;
              collectedQuantity -= x.quantity;
            } else {
              x.collectedQuantity = collectedQuantity;
              collectedQuantity = 0;
              x.collected = x.collectedQuantity == x.quantity;
            }
          }
        });

      this.debounceSaveOrder();
    },
    goToOrderList: function() {
      this.$router.push({
        name: "Orders"
      });
    },
    collectedQuantityChanged: function(item, qty) {
      item.collectedQuantity = qty;
      this.setCollectedItem(item);
    },
    debounceSaveOrder: function() {
      if (saveOrderTimeoutFunc) window.clearTimeout(saveOrderTimeoutFunc);

      saveOrderTimeoutFunc = window.setTimeout(
        () => this.$fb.orderCollection.doc(this.order.id).set(this.order),
        500
      );
    }
  }
};
</script>
