<template>
  <b-modal :id="modalId" :title="modalTitle" @ok="save" size="lg">
    <b-row>
      <b-col sm="8">
        <b-form-group>
          <label>Product</label>
          <vue-suggestion
            :items="sugProducts"
            v-model="item.product"
            :item-template="productSugTemplate"
            :setLabel="setLabel"
            @focus="sugProductsFocused"
            @onInputChange="sugProductsInputChanged"
            @onItemSelected="sugProductsItemSelected"
            @scrollEnd="loadMoreSugProducts"
          ></vue-suggestion>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label>Quantity</label>
          <number-input v-model="item.quantity" :min="1" :max="9999" inline controls center></number-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="12">
        <b-form-group>
          <label>Note</label>
          <b-form-textarea v-model="item.note" placeholder="Enter something..." rows="5"></b-form-textarea>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import vueSuggestion from "../shared/vueSuggestion";
import productSugTemplate from "./productSugTemplate";
import numberInput from "../shared/numberInput";
var loadCustomerTimeout;
export default {
  name: "InventoryEditItem",
  props: ["id"],
  components: { vueSuggestion, numberInput },
  data() {
    return {
      item: {},
      sugProducts: [],
      modalTitle: "",
      modalId: "",
      productSugTemplate: productSugTemplate
    };
  },
  mounted: function() {
    this.modalId = this.id || "inventoryEditItemModal";
  },
  watch: {},
  computed: {},
  methods: {
    open: function(item) {
      if (!item)
        this.item = {
          quantity: 1,
          note: "",
          product: {},
          dateIn: new Date()
        };
      else this.item = { ...item };
      this.modalTitle = item ? "Edit Order Item" : "Add Order Item";
      this.$nextTick(() => this.$bvModal.show(this.modalId));
    },
    sugProductsFocused: function() {
      if (this.sugProducts.length == 0) {
        //load first 5 customers
        this.loadProducts().then(products => products.forEach(item => this.sugProducts.push(item)));
      }
    },
    sugProductsInputChanged: function(searchText) {
      this.productSugSearchText = searchText;
      this.sugProducts = [];
      this.lastSugProductVisible = undefined;

      if (loadProductTimeout) window.clearTimeout(loadProductTimeout);

      loadProductTimeout = this.loadProducts(searchText).then(products => {
        products.forEach(item => this.sugProducts.push(item));
      });
    },
    loadMoreSugProducts: function(itemCount) {
      if (this.isLoadingMoreSugProduct) return;

      this.isLoadingMoreSugProduct = true;
      this.loadProducts(this.productSugSearchText, this.lastSugProductVisible).then(products => {
        products.forEach(item => this.sugProducts.push(item));
        this.isLoadingMoreSugProduct = false;
      });
    },
    loadProducts: function(searchText, lastVisible) {
      var query = this.$fb.productCollection.limit(5).orderBy("name");
      if (searchText)
        query = query.where("nameSearch", "array-contains", searchText.toLowerCase().trim());
      if (lastVisible) query = query.startAfter(lastVisible);

      return query.get().then(snapshot => {
        var products = [];
        if (!snapshot.empty) {
          this.lastSugProductVisible = _.last(snapshot.docs);

          snapshot.forEach(doc => {
            products.push(doc.data());
          });
        }

        return products;
      });
    },
    sugProductsItemSelected: function(item) {},
    setLabel: function(item) {
      return item.name;
    },
    save: async function() {
      //handle saving data
      if (this.item.id) {
        if (!this.item.audit) this.item.audit = [];
        this.item.audit.push(this.$fbServices.inventoryService.buildAuditItem(this.item, "Update"));
        await this.$fb.inventoryCollection.doc(this.item.id).set(this.item);
      } else {
        var docRef = this.$fb.inventoryCollection.doc();
        this.item.id = docRef.id;
        if (!this.item.audit) this.item.audit = [];
        this.item.audit.push(this.$fbServices.inventoryService.buildAuditItem(this.item, "Init"));
        await docRef.set(this.item);
        this.item = await docRef.get().then(ss => ss.data());
      }
      this.showMessage("Item saved!");
      this.$emit("item-saved", this.item);
      this.$bvModal.hide(this.id);
    }
  }
};
</script>
