import fb from "../firebaseConfig"
const productCollecion = fb.db.collection('product')

export default {
  getProducts: function (limit, searchText, lastVisible) {
    return this.$fb.productCollection
      .orderBy("name")
      .startAfter(this.lastVisible || 0)
      .limit(this.pageSize)
      .get()
      .then(snapshot => {
        if (!snapshot.empty) {
          this.lastVisible = _.last(snapshot.docs);
          snapshot.forEach(doc => {
            this.products.push(doc.data());
          });
        }
      });

  }
}