export function random(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

/**
 * Randomize array element order in-place.
 * Using Durstenfeld shuffle algorithm.
 */
export const shuffleArray = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1))
    let temp = array[i]
    array[i] = array[j]
    array[j] = temp
  }
  return array
}


export function generateSubString(str) {
  var strArray = str.split(" ");
  var result = [];
  strArray.forEach(sub => {
    var pre = "";
    sub.split("").forEach(s => {
      pre += s.toLowerCase();
      result.push(pre);
    })
  })
  return result;
}

Date.prototype.setMin = function () {
  this.setHours(0);
  this.setMinutes(0);
  this.setSeconds(0);
  this.setMilliseconds(0);
  return this;
}

Number.prototype.roundTo = function (decimalPlaces) {
  return Math.round(this * Number("1e" + decimalPlaces)) / Number("1e" + decimalPlaces);

}


if (!String.prototype.format) {
  String.prototype.format = function () {
    var args = arguments;
    return this.replace(/{(\d+)}/g, function (match, number) {
      return typeof args[number] != 'undefined'
        ? args[number]
        : match
        ;
    });
  };
}