<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-user-o"></i>
              Customers ({{count}})
            </span>
            <div class="pull-right">
              <b-link @click="calculateTotalBuying">Calculate Total Buying |&nbsp;</b-link>
              <b-link @click="addNewCustomer">Add New Customer</b-link>
            </div>
            <!-- <b-link class="pull-right" @click="correctData">Correct data</b-link> -->
          </div>

          <b-row>
            <b-col sm="12" md="6">
              <b-input-group class="mb-5">
                <b-form-input v-model="searchText" @keydown="seartTextKeydown($event)"></b-form-input>

                <b-input-group-append>
                  <span
                    id="searchclear"
                    class="fa fa-close"
                    v-if="searchText != ''"
                    @click="clearSearchText"
                  ></span>
                  <b-button variant="outline-success" @click="searchCustomer()">
                    <span class="fa fa-search"></span> Search
                  </b-button>
                </b-input-group-append>
              </b-input-group>

              <b-form-group>
                <b-form-checkbox
                  v-model="filter.showOnlyDebt"
                  @change="showOnlyDebtChange"
                  inline
                >Show Only Debt</b-form-checkbox>
              </b-form-group>
            </b-col>
          </b-row>

          <view-port>
            <template v-slot:desktop>
              <b-table
                ref="customerTable"
                responsive="sm"
                :items="customers"
                :fields="fields"
                :small="true"
                selectable
                select-mode="single"
                @row-clicked="customerClicked"
                @row-dblclicked="customerDoubleClicked"
              >
                <template v-slot:cell(index)="data">{{data.index + 1}}</template>
                <template v-slot:cell(name)="data">
                  <template v-if="data.item.isEditing">
                    <b-form-input
                      type="text"
                      v-model="data.item.name"
                      placeholder="Enter customer name"
                    ></b-form-input>
                  </template>
                  <template v-if="!data.item.isEditing">{{data.value}}</template>
                </template>
                <template v-slot:cell(address)="data">
                  <template v-if="data.item.isEditing">
                    <b-form-input
                      type="text"
                      v-model="data.item.address"
                      placeholder="Enter customer address"
                    ></b-form-input>
                  </template>
                  <template v-if="!data.item.isEditing">{{data.value}}</template>
                </template>
                <template v-slot:cell(phone)="data">
                  <template v-if="data.item.isEditing">
                    <b-form-input
                      type="text"
                      v-model="data.item.phone"
                      placeholder="Enter customer phone"
                    ></b-form-input>
                  </template>
                  <template v-if="!data.item.isEditing">{{data.value}}</template>
                </template>
                <template
                  v-slot:cell(statistic.totalBuying)="data"
                >{{data.value | currency(vndConfig)}}</template>
                <template
                  v-slot:cell(statistic.purchased)="data"
                >{{data.value | currency(vndConfig)}}</template>
                <template v-slot:cell(statistic.debt)="data">{{data.value | currency(vndConfig)}}</template>
                <template v-slot:cell(action)="data">
                  <template v-if="!data.item.isEditing">
                    <b-button
                      type="submit"
                      class="mr-1"
                      size="sm"
                      variant="info"
                      @click="viewOrders(data)"
                    >
                      <i class="fa fa-list"></i> Orders
                    </b-button>
                    <b-button
                      type="submit"
                      class="mr-1"
                      size="sm"
                      variant="success"
                      @click="editCustomer(data)"
                    >
                      <i class="fa fa-edit"></i> Edit
                    </b-button>
                    <b-button
                      type="submit"
                      size="sm"
                      variant="danger"
                      @click="deleteCustomer(data)"
                    >
                      <i class="fa fa-remove"></i> Delete
                    </b-button>
                  </template>
                  <template v-if="data.item.isEditing">
                    <b-button
                      type="submit"
                      class="mr-1"
                      size="sm"
                      variant="success"
                      @click="saveCustomer(data)"
                    >
                      <i class="fa fa-edit"></i> Save
                    </b-button>
                    <b-button
                      type="submit"
                      size="sm"
                      variant="danger"
                      @click="cancelEditCustomer(data)"
                    >
                      <i class="fa fa-remove"></i> Cancel
                    </b-button>
                  </template>
                </template>
              </b-table>
            </template>

            <template v-slot:mobile>
              <div>
                <b-table
                  ref="customerTable"
                  responsive="sm"
                  :items="customers"
                  :fields="mobileFields"
                  :small="true"
                  selectable
                  select-mode="single"
                  @row-clicked="customerClicked"
                  @row-dblclicked="customerDoubleClicked"
                >
                  <template v-slot:cell(index)="data">{{data.index + 1}}</template>
                  <template v-slot:cell(name)="data">
                    <b-row>
                      <b-col>
                        <strong>{{data.index + 1}}. {{data.item.name}}</strong>
                      </b-col>
                    </b-row>
                  </template>

                  <template v-slot:cell(name)="data">
                    <b-row>
                      <b-col>
                        <template v-if="data.item.isEditing">
                          <b-form-group label-size="sm" label="Name">
                            <b-form-input
                              size="sm"
                              type="text"
                              v-model="data.item.name"
                              placeholder="Enter customer name"
                            ></b-form-input>
                          </b-form-group>
                        </template>

                        <template v-if="!data.item.isEditing">
                          <strong>{{data.value}}</strong>
                        </template>
                      </b-col>
                    </b-row>
                    <b-row>
                      <b-col>
                        <template v-if="data.item.isEditing">
                          <b-form-group label-size="sm" label="Address">
                            <b-form-input
                              size="sm"
                              type="text"
                              v-model="data.item.address"
                              placeholder="Enter customer address"
                            ></b-form-input>
                          </b-form-group>
                        </template>
                        <template v-if="!data.item.isEditing">Address: {{data.item.address}}</template>
                      </b-col>
                    </b-row>
                    <b-row>
                      <b-col>
                        <template v-if="data.item.isEditing">
                          <b-form-group label-size="sm" label="Phone">
                            <b-form-input
                              size="sm"
                              type="text"
                              v-model="data.item.phone"
                              placeholder="Enter customer phone"
                            ></b-form-input>
                          </b-form-group>
                        </template>
                        <template v-if="!data.item.isEditing">Phone: {{data.item.phone}}</template>
                      </b-col>
                    </b-row>
                    <b-row class="mt-2 mb-2">
                      <b-col>
                        <template v-if="!data.item.isEditing">
                          <b-button
                            type="submit"
                            class="mr-1"
                            size="sm"
                            variant="info"
                            @click="viewOrders(data)"
                          >
                            <i class="fa fa-list"></i> Orders
                          </b-button>
                          <b-button
                            type="submit"
                            class="mr-1"
                            size="sm"
                            variant="success"
                            @click="editCustomer(data)"
                          >
                            <i class="fa fa-edit"></i> Edit
                          </b-button>
                          <b-button
                            type="submit"
                            size="sm"
                            variant="danger"
                            @click="deleteCustomer(data)"
                          >
                            <i class="fa fa-remove"></i> Delete
                          </b-button>
                        </template>
                        <template v-if="data.item.isEditing">
                          <b-button
                            type="submit"
                            class="mr-1"
                            size="sm"
                            variant="success"
                            @click="saveCustomer(data)"
                          >
                            <i class="fa fa-edit"></i> Save
                          </b-button>
                          <b-button
                            type="submit"
                            size="sm"
                            variant="danger"
                            @click="cancelEditCustomer(data)"
                          >
                            <i class="fa fa-remove"></i> Cancel
                          </b-button>
                        </template>
                      </b-col>
                    </b-row>
                  </template>
                </b-table>
              </div>
            </template>
          </view-port>

          <b-row class="load-more">
            <b-button variant="outline-success" @click="loadMore" v-loading="isLoading">Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";
import { generateSubString } from "@/shared/utils";
import viewPort from "../shared/viewPort";

export default {
  name: "Customers",
  components: { viewPort },
  data() {
    return {
      customers: [],
      customer: {},
      searchText: "",
      fields: [
        { key: "index", label: "#", class: "customer-index-col" },
        { key: "name", class: "customer-name-col" },
        { key: "address", class: "customer-address-col" },
        { key: "phone", class: "customer-phone-col" },
        { key: "statistic.totalBuying", label: "Total Buying", class: "customer-phone-col" },
        { key: "statistic.purchased", label: "Purchased", class: "customer-phone-col" },
        { key: "statistic.debt", label: "Debt", class: "customer-phone-col" },
        { key: "action", class: "customer-action-col" }
      ],
      mobileFields: [
        { key: "index", label: "#" },
        { key: "name", label: "Details" }
      ],
      paging: {
        total: 0,
        page: 1,
        size: 10
      },
      lastVisible: undefined,
      count: 0,
      filter: {
        showOnlyDebt: false
      },
      isLoading: true
    };
  },
  mounted: function() {
    this.loadCustomers();
    this.$fb.counterService.getCustomerCount().then(count => (this.count = count));
  },
  watch: {
    searchText: function(newValue, oldValue) {
      if (oldValue != "" && newValue == "") {
        this.customers = [];
        this.lastVisible = undefined;
        this.loadCustomers();
      }
    }
  },
  computed: {},
  methods: {
    loadCustomers: function() {
      this.isLoading = true;
      var query = this.$fb.customerCollection.limit(this.paging.size);

      if (this.filter && this.filter.showOnlyDebt) {
        query = query.where("statistic.debt", ">", 0).orderBy("statistic.debt", "desc");
      } else {
        query = query.orderBy("name");
      }

      if (this.lastVisible) query = query.startAfter(this.lastVisible);

      query.get().then(snapshot => {
        this.isLoading = false;
        if (!snapshot.empty) {
          this.lastVisible = snapshot.docs[snapshot.docs.length - 1];

          snapshot.forEach(doc => {
            var data = doc.data();
            data.id = doc.id;
            this.customers.push(data);
          });
        }
      });
    },
    customerClicked: function(customer) {
      this.customer = { ...customer };
    },
    correctData: function() {
      // this.customers.forEach(
      //   cus => (cus.nameSearch = generateSubString(cus.name))
      // );
      // this.customers.forEach(cus =>
      //   this.$fb.customerCollection.doc(cus.id).set(cus)
      // );
    },
    deleteCustomer: function(data) {
      var customer = data.item;
      var index = data.index;
      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete customer: [${customer.name}]?`)
        .then(value => {
          if (value) {
            this.$fb.customerCollection
              .doc(customer.id)
              .delete()
              .then(() => {
                this.customers.splice(index, 1);
                this.$fb.counterService.decreaseCustomer().then(count => (this.count = count));
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    loadMore: function() {
      this.loadCustomers();
    },
    seartTextKeydown: function(e) {
      if (e.keyCode === 13) this.searchCustomer();
    },
    clearSearchText: function() {
      this.searchText = "";
    },
    searchCustomer: function(lastVisible) {
      if (!this.searchText) return;

      var query = this.$fb.customerCollection
        .where("nameSearch", "array-contains", this.searchText.toLowerCase().trim())
        .orderBy("name")
        .startAfter(lastVisible || 0)
        .limit(10)
        .get()
        .then(snapshot => {
          this.customers = [];
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              var data = doc.data();
              data.id = doc.id;
              this.customers.push(data);
            });
          }
        });
    },
    editCustomer: function(data) {
      var { item, index } = data;
      item.backup = { ...item };
      item.isEditing = true;
      this.customers.splice(index, 1, item);
    },
    saveCustomer: function(data) {
      var customer = data.item;
      var index = data.index;
      if (!customer.name) return;
      delete customer.backup;
      delete customer.isEditing;
      return this.submitCustomer(customer).then(cus => {
        this.customers.splice(index, 1, cus);
      });
    },
    submitCustomer: function(customer) {
      customer.nameSearch = generateSubString(customer.name);
      customer.version = (customer.version || 0) + 1;
      if (customer.id) {
        //update
        return this.$fb.customerCollection
          .doc(customer.id)
          .set(customer)
          .then(() => {
            return customer;
          });
      } else {
        var cusRef = this.$fb.customerCollection.doc();
        customer.id = cusRef.id;
        return cusRef.set(customer).then(() => {
          this.increaseCustomerCount();
          return customer;
        });
      }
    },
    increaseCustomerCount: function() {
      this.$fb.counterService.increaseCustomer().then(count => (this.count = count));
    },
    cancelEditCustomer: function(data) {
      var { item, index } = data;
      if (item.id) {
        this.customers.splice(index, 1, item.backup);
      } else {
        this.customers.splice(index, 1);
      }
    },
    addNewCustomer: function() {
      var newCus = {
        name: "",
        address: "",
        phone: "",
        isEditing: true
      };
      this.customers.unshift(newCus);
    },
    customerDoubleClicked: function(item, index, event) {
      this.editCustomer({ index: index, item: item });
    },
    viewOrders: function(data) {
      var { index, item } = data;
      this.$router.push({
        name: "CustomerOrders",
        params: { id: item.id }
      });
    },
    calculateTotalBuying: async function() {
      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to calculate total buying ?`)
        .then(async value => {
          if (value) {
            var orders = await this.loadOrders();
            var customers = await this.loadAllCustomers();

            await Promise.all(
              customers.map(async customer => {
                var obj = this.calculateTotalBuyingForCustomer(customer, orders);
                customer.statistic = obj;
                console.log(customer);
                await this.$fb.customerCollection.doc(customer.id).set(customer);
              })
            );

            this.$bvModal
              .msgBoxConfirm(`Calculate successfully, Click Ok to refresh page!`)
              .then(v => {
                if (v) {
                  this.customers = [];
                  this.lastVisible = undefined;
                  this.loadCustomers();
                }
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    calculateTotalBuyingForCustomer: function(customer, orders) {
      var customerOrderItems = orders
        .filter(
          x =>
            x.items &&
            x.items.length > 0 &&
            x.items.find(i => i.customer && i.customer.id == customer.id) != undefined
        )
        .reduce((m, i) => m.concat(i.items), [])
        .filter(x => x.customer && x.customer.id == customer.id);

      return {
        totalBuying: _.sumBy(
          customerOrderItems,
          x => parseFloat(x.rndPrice) * parseFloat(x.quantity)
        ),
        purchased: _.sumBy(
          customerOrderItems.filter(x => x.purchased),
          x => parseFloat(x.rndPrice) * parseFloat(x.quantity)
        ),
        debt: _.sumBy(
          customerOrderItems.filter(x => !x.purchased),
          x => parseFloat(x.rndPrice) * parseFloat(x.quantity)
        )
      };
    },
    loadOrders: async function() {
      return this.$fb.orderCollection
        .orderBy("dateCreated")
        .get()
        .then(ss => {
          if (ss.empty) return [];
          else {
            return ss.docs.map(doc => doc.data());
          }
        });
    },
    loadAllCustomers: async function() {
      return this.$fb.customerCollection.get().then(ss => {
        if (ss.empty) return [];
        else {
          return ss.docs.map(doc => doc.data());
        }
      });
    },
    showOnlyDebtChange: function(checked) {
      this.customers = [];
      this.lastVisible = undefined;
      this.$nextTick(() => {
        this.loadCustomers();
      });
    }
  }
};
</script>
