<template>
  <div>
    <!-- use mobile view: {{useMobileView}}
    <br />
    inner width: {{innerWidth}}-->
    <slot name="mobile" v-if="useMobileView">This is mobile view</slot>
    <slot name="desktop" v-if="!useMobileView">This is desktop view</slot>
  </div>
</template>
<script>
export default {
  name: "viewPort",
  props: [],
  data: function() {
    return {
      useMobileView: false,
      innerWidth: 0
    };
  },
  mounted: function() {
    var self = this;
    self.useMobileView = window.innerWidth < 700;
    self.innerWidth = window.innerWidth;
    window.addEventListener("resize", function() {
      self.useMobileView = window.innerWidth < 700;
      self.innerWidth = window.innerWidth;
    });
  },
  computed: {}
};
</script>