<template>
  <b-table
    ref="productTable"
    responsive="sm"
    :items="items"
    :fields="fields"
    :small="true"
    @row-clicked="productClicked"
    @row-dblclicked="productDoubleClicked"
  >
    <template v-slot:cell(name)="data">
      <div class="row">
        <div class="col-4" style="overflow: hidden">
          <img :src="data.item.thumbnail" width="150px" height="150px" style="margin-left: -30px" />
        </div>
        <div class="col-8">
          <strong>
            <text-highlight :queries="queries">{{data.item.name}}</text-highlight>
          </strong>
          <br />
          <strong>Weight:</strong>
          {{data.item.weight}}(g)
          - {{data.item.shipPrice | currency(audConfig)}}
          <br />
          <show-supplier-prices :product="data.item"></show-supplier-prices>
          <span class="h4">
            <strong>Price</strong>
            : {{data.item.rndPrice | currency(vndConfig)}}
          </span>

          <div style="margin-top: 7px">
            <b-button
              type="submit"
              size="sm"
              variant="success"
              @click="editProduct(data)"
              class="mr-1"
            >
              <i class="fa fa-edit"></i> Edit
            </b-button>
            <b-button type="submit" size="sm" variant="danger" @click="deleteProduct(data)">
              <i class="fa fa-remove"></i> Delete
            </b-button>
          </div>
        </div>
      </div>
    </template>
  </b-table>
</template>

<script>
import productList from "./productsList";
import ShowSupplierPrices from "./ShowSupplierPrices";

export default {
  name: "ProductsList",
  components: { ShowSupplierPrices },
  props: [],
  extends: productList,
  data() {
    return {
      fields: [{ key: "name", label: "", class: "" }]
    };
  },
  mounted: function() {},
  computed: {},
  methods: {}
};
</script>
