<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <b-link @click="goToCustomers">
              <i class="fa fa-user-o"></i> Customers:
            </b-link>
            <strong>{{customer.name}}</strong>'s Orders
            <!-- <b-link class="pull-right" @click="recalculateOrders">Recalculate Orders</b-link> -->
          </div>
          <b-row>
            <b-col cols="6" lg="1">Total Buying</b-col>
            <b-col cols="6" lg="2" class="text-right">
              <strong>{{totalBuying | currency(vndConfig)}}</strong>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="6" lg="1">Purchased</b-col>
            <b-col cols="6" lg="2" class="text-right">
              <strong>{{purchased | currency(vndConfig)}}</strong>
            </b-col>
          </b-row>
          <b-row style="margin-bottom: 20px;">
            <b-col cols="6" lg="1">Debt</b-col>
            <b-col cols="6" lg="2" class="text-right">
              <strong>{{debt | currency(vndConfig)}}</strong>
            </b-col>
          </b-row>
          <view-port>
            <template v-slot:desktop>
              <div>
                <b-table
                  responsive="sm"
                  :items="customerOrders"
                  :fields="fields"
                  :small="true"
                  :hover="true"
                >
                  <template v-slot:cell(index)="data">{{data.index +1}}</template>
                  <template v-slot:cell(name)="data">
                    <a
                      style="font-size: 15px"
                      :href="buildRoute(data.item)"
                      variant="link"
                    >{{data.item.name}}</a>
                  </template>
                  <template v-slot:cell(products)="data">
                    <table class="table table-borderless product-table">
                      <tr v-for="(item, index) in data.item.items" :key="index">
                        <td>{{item.product.name}}</td>
                        <td class="text-right" style="width: 5%">{{item.quantity}}</td>
                        <td
                          class="text-right"
                          style="width: 25%"
                        >{{item.rndPrice |currency(vndConfig)}}</td>
                      </tr>
                    </table>
                  </template>

                  <template v-slot:cell(totalQuantity)="data">
                    <strong>{{data.value}}</strong>
                  </template>
                  <template v-slot:cell(totalPrice)="data">
                    <strong>{{data.value | currency(vndConfig)}}</strong>
                  </template>
                  <template v-slot:cell(purchased)="data">
                    <b-form-checkbox v-model="data.item.purchased"></b-form-checkbox>
                  </template>
                </b-table>
              </div>
            </template>
            <template v-slot:mobile>
              <b-table
                responsive="sm"
                :items="customerOrders"
                :fields="mobileFields"
                :small="true"
                :hover="true"
              >
                <template v-slot:cell(index)="data">
                  <b-row>
                    <b-col>
                      <strong>
                        {{data.index + 1}}.
                        <a
                          style="font-size: 15px"
                          :href="buildRoute(data.item)"
                          variant="link"
                        >{{data.item.name}}</a>
                      </strong>
                    </b-col>
                    <b-col class="text-right">
                      <b-form-checkbox v-model="data.item.purchased" :disabled="true">Purchased</b-form-checkbox>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col>
                      <table class="table table-borderless product-table">
                        <tr v-for="(item, index) in data.item.items" :key="index">
                          <td>{{item.product.name}}</td>
                          <td class="text-right" style="width: 5%">{{item.quantity}}</td>
                          <td
                            class="text-right"
                            style="width: 27%"
                          >{{item.rndPrice |currency(vndConfig)}}</td>
                        </tr>
                        <tr>
                          <td>Total</td>
                          <td class="text-right" style="width: 5%">
                            <strong>{{data.item.totalQuantity}}</strong>
                          </td>
                          <td class="text-right" style="width: 27%">
                            <strong>{{data.item.totalPrice | currency(vndConfig)}}</strong>
                          </td>
                        </tr>
                      </table>
                    </b-col>
                  </b-row>
                </template>
              </b-table>
            </template>
          </view-port>
        </b-card>
      </b-col>
    </b-row>

    <!--/.row-->
  </div>
</template>

<script>
import viewPort from "../shared/viewPort";
export default {
  name: "Orders",
  components: { viewPort },
  data() {
    return {
      customer: {},
      customerOrders: [],
      fields: [
        { key: "index", label: "#", class: "shipment-index-col" },
        {
          key: "name",
          label: "Order name"
        },
        { key: "products", label: "Products", class: "customer-order-product-col" },

        {
          key: "totalQuantity",
          label: "Total Qty",
          class: "text-center shipment-quantity-col",
          tdClass: "vertical-middle"
        },
        {
          key: "totalPrice",
          label: "Total Price",
          class: "text-right shipment-price-col"
        },
        {
          key: "purchased",
          label: "Purchased",
          class: "text-center shipment-purchased-col"
        }
      ],
      mobileFields: [{ key: "index" }]
    };
  },
  mounted: function() {
    this.loadCustomer();
  },
  computed: {
    debt: function() {
      if (!this.customerOrders) return 0;
      return _.chain(this.customerOrders)
        .filter(x => x.purchased == false)
        .sumBy(x => x.totalPrice)
        .value();
    },
    totalBuying: function() {
      if (!this.customerOrders) return 0;
      return _.chain(this.customerOrders)
        .sumBy(x => x.totalPrice)
        .value();
    },
    purchased: function() {
      if (!this.customerOrders) return 0;
      return _.chain(this.customerOrders)
        .filter(x => x.purchased == true)
        .sumBy(x => x.totalPrice)
        .value();
    }
  },
  methods: {
    loadCustomer: function() {
      var customerId = this.$route.params.id;
      this.$fb.customerCollection
        .doc(customerId)
        .get()
        .then(ss => (this.customer = ss.data()))
        .then(this.loadCustomerOrders);
    },
    loadCustomerOrders: async function() {
      var orders = await this.loadOrders();
      this.customerOrders = await this.buildCustomerOrders(orders);
      console.log(this.customerOrders);
    },
    goToCustomers: function() {
      this.$router.push({ name: "Customers" });
    },
    recalculateOrders: async function() {
      var orders = await this.loadOrders();
      this.customerOrders = await this.buildCustomerOrders(orders);
      console.log(this.customerOrders);
    },
    buildCustomerOrders: async function(orders) {
      var customerOrders = orders.filter(
        x =>
          x.items &&
          x.items.length > 0 &&
          x.items.find(i => i.customer && i.customer.id == this.customer.id) != undefined
      );
      customerOrders.forEach(order => {
        order.items = order.items.filter(x => x.customer && x.customer.id == this.customer.id);
        order.totalQuantity = _.sumBy(order.items, x => x.quantity);
        order.totalPrice = _.sumBy(
          order.items,
          x => parseFloat(x.rndPrice) * parseFloat(x.quantity)
        );
        order.purchased = _.find(order.items, x => !x.purchased) == undefined;
      });
      return _.orderBy(customerOrders, "dateCreated", "desc");
    },
    loadOrders: async function() {
      return this.$fb.orderCollection
        .orderBy("dateCreated")
        .get()
        .then(ss => {
          if (ss.empty) return [];
          else {
            return ss.docs.map(doc => doc.data());
          }
        });
    },
    buildRoute: function(order) {
      var { origin } = location;
      return this.$router.resolve({
        name: "OrderDetail",
        params: { id: order.id }
      }).href;
    }
  }
};
</script>
<style scoped>
.product-table {
  margin-bottom: 0;
}
.product-table tr:hover {
  background-color: inherit;
}
</style>