<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <b-link @click="goToOrderList">
              <i class="fa fa-server"></i> Order:
            </b-link>
            <strong>{{order.name}}</strong>
            <div class="pull-right">
              <b-link @click="goToShipmentPage" class="mr-1">Shipment Page |</b-link>
              <b-link @click="goToCollectionPage" class="mr-1">Collection Page |</b-link>
              <b-link @click="addNewOrderItem">Add New Item</b-link>
            </div>
          </div>
          <view-port>
            <template v-slot:desktop>
              <div class="order-item">
                <b-table
                  foot-clone
                  responsive="sm"
                  :items="order.items"
                  :fields="fields"
                  :small="true"
                  :hover="true"
                  selectable
                  select-mode="single"
                  @row-clicked="orderItemClicked"
                >
                  <template v-slot:cell(index)="data">{{data.index +1}}</template>
                  <template v-slot:cell(product.name)="data">
                    <a
                      href="javascript:void(0)"
                      @click="showProductDetailModal(data.item)"
                    >{{data.value}}</a>
                    <b-link
                      :href="appSettings.cwBuyProductUrl.replace('{id}', data.item.product.cwProductId)"
                      target="_blank"
                      v-if="data.item.product.cwProductId"
                    >[Buy CW]</b-link>
                    <span v-if="data.item.distribution" class="ml-2">
                      <i>From Stock</i>
                    </span>
                    <br />
                    <show-supplier-prices :product="data.item.product"></show-supplier-prices>
                  </template>
                  <template v-slot:cell(price)="data">
                    <del
                      style="color: darkgrey"
                      v-if="data.item.rndPrice != data.item.product.rndPrice"
                    >{{data.item.product.rndPrice | currency(vndConfig)}}</del>
                    {{data.item.rndPrice | currency(vndConfig)}}
                  </template>
                  <template
                    v-slot:cell(total)="data"
                  >{{calculateTotalOrderItem(data.item) | currency(vndConfig)}}</template>
                  <template v-slot:cell(collectedQuantity)="data">
                    <number-input
                      v-model="data.item.collectedQuantity"
                      :min="0"
                      :max="data.item.quantity"
                      inline
                      controls
                      center
                      size="sm"
                      @change="collectedQuantityChanged(data.item, $event)"
                      :disabled="!!data.item.distribution"
                    ></number-input>
                  </template>
                  <template v-slot:cell(collected)="data">
                    <b-form-checkbox
                      v-model="data.item.collected"
                      @change="collectedChanged($event, data.item)"
                      :disabled="!!data.item.distribution"
                    ></b-form-checkbox>
                  </template>
                  <template v-slot:cell(action)="data">
                    <b-button
                      type="submit"
                      size="sm"
                      variant="success"
                      @click="editOrderItem(data.item)"
                      class="mr-1"
                    >
                      <i class="fa fa-edit"></i> Edit
                    </b-button>
                    <b-button
                      type="submit"
                      size="sm"
                      variant="danger"
                      @click="deleteOrderItem(data.item)"
                    >
                      <i class="fa fa-remove"></i> Delete
                    </b-button>
                  </template>

                  <!-- footer -->
                  <template v-slot:foot()="data"></template>
                  <template v-slot:foot(quantity)="data">{{sumQuantity}}</template>
                  <template v-slot:foot(total)="data">{{sumTotal | currency(vndConfig)}}</template>
                  <template v-slot:foot(collectedQuantity)="data">{{sumCollectedQuantity}}</template>
                </b-table>
              </div>
            </template>
            <template v-slot:mobile>
              <b-table
                foot-clone
                responsive="sm"
                :items="order.items"
                :fields="mobileFields"
                :small="true"
                selectable
                select-mode="single"
                @row-clicked="orderItemClicked"
              >
                <template v-slot:cell(index)="data">
                  <div>
                    <strong>
                      {{data.index +1}}.
                      {{data.item.customer.name}}
                    </strong>
                  </div>
                  <div>
                    <a href="javascript:void(0)" @click="showProductDetailModal(data.item)">
                      {{data.item.product.name}}
                      <small
                        v-if="!data.item.product.buyPrice || data.item.product.buyPrice == 0"
                      >
                        <i style="color: red">missing buy price</i>
                      </small>
                    </a>
                    <b-badge v-if="data.item.product.bvSalePricePick" variant="success">BV Sale</b-badge>
                    <b-badge v-if="data.item.product.cwSalePricePick" variant="success">CW Sale</b-badge>&nbsp;
                    <b-link
                      :href="appSettings.cwBuyProductUrl.replace('{id}', data.item.product.cwProductId)"
                      target="_blank"
                      v-if="data.item.product.cwProductId"
                    >[Buy CW]</b-link>
                  </div>
                  <div class="row">
                    <div class="col-6">
                      <small>
                        <del
                          style="color: darkgrey"
                          v-if="data.item.rndPrice != data.item.product.rndPrice"
                        >{{data.item.product.rndPrice | currency(vndConfig)}}</del>
                      </small>
                      {{data.item.rndPrice | currency(vndConfig)}}
                    </div>
                    <div class="col-2">{{data.item.quantity}}</div>
                    <div class="col-4">{{calculateTotalOrderItem(data.item) | currency(vndConfig)}}</div>
                  </div>
                  <div class="row" style="margin-top: 10px">
                    <div class="col-6">
                      <div class="order-detail-mobile-collected-qty">
                        <number-input
                          v-model="data.item.collectedQuantity"
                          :min="0"
                          :max="data.item.quantity"
                          inline
                          controls
                          center
                          size="sm"
                          @change="collectedQuantityChanged(data.item, $event)"
                        ></number-input>
                      </div>
                      <div class="order-detail-mobile-collected-all">
                        <b-form-checkbox
                          v-model="data.item.collected"
                          @change="collectedChanged($event, data.item)"
                        ></b-form-checkbox>
                      </div>
                    </div>
                    <div class="col-6 pull-right">
                      <b-button
                        type="submit"
                        size="sm"
                        variant="success"
                        @click="editOrderItem(data.item)"
                        class="mr-1"
                      >
                        <i class="fa fa-edit"></i> Edit
                      </b-button>
                      <b-button
                        type="submit"
                        size="sm"
                        variant="danger"
                        @click="deleteOrderItem(data.item)"
                      >
                        <i class="fa fa-remove"></i> Delete
                      </b-button>
                    </div>
                  </div>
                </template>
                <template v-slot:foot()="data">
                  <hr />
                  <b-row>
                    <b-col>Quantity</b-col>
                    <b-col class="text-right">{{sumQuantity}}</b-col>
                  </b-row>
                  <b-row>
                    <b-col>Total</b-col>
                    <b-col class="text-right">{{sumTotal | currency(vndConfig)}}</b-col>
                  </b-row>
                  <b-row>
                    <b-col>Collected Qty</b-col>
                    <b-col class="text-right">{{sumCollectedQuantity}}</b-col>
                  </b-row>
                </template>
              </b-table>
            </template>
          </view-port>
        </b-card>
      </b-col>
    </b-row>

    <product-detail ref="productDetail" />

    <order-detail-edit-item
      ref="editItemModal"
      id="editItem"
      :item="selectedOrderItem"
      @save-order-item="saveOrderItem"
    ></order-detail-edit-item>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";
import _ from "lodash";
import numberInput from "../shared/numberInput";
import vndPrice from "../shared/vndPrice";
import productDetail from "./productDetail";
import orderDetailEditItem from "./orderDetailEditItem";
import viewPort from "../shared/viewPort";
import ShowSupplierPrices from "./ShowSupplierPrices";
var saveOrderTimeoutFunc;
export default {
  name: "OrderDetail",
  components: {
    numberInput,
    vndPrice,
    productDetail,
    orderDetailEditItem,
    viewPort,
    ShowSupplierPrices
  },
  data() {
    return {
      order: [],
      counter: {},
      selectedOrderItem: undefined,
      fields: [
        { key: "index", label: "#" },
        { key: "customer.name", label: "Customer", sortable: true },
        { key: "product.name", label: "Product" },
        { key: "price", label: "Price", class: "text-right" },
        { key: "quantity", label: "Quantity", class: "text-right" },
        { key: "total", label: "Total", class: "text-right" },
        {
          key: "collectedQuantity",
          label: "Collected Qty",
          class: "text-center order-detail-collected-quantity"
        },
        { key: "collected", label: "Collected", class: "text-center" },
        { key: "action", class: "text-right" }
      ],
      mobileFields: [{ key: "index", label: "" }],
      product: {}
    };
  },
  mounted: function() {
    this.loadOrder();
  },
  watch: {},
  computed: {
    sumQuantity: function() {
      if (!this.order.items || this.order.items.length == 0) return 0;
      return this.order.items.reduce((s, x) => (s += x.quantity), 0);
    },
    sumCollectedQuantity: function() {
      if (!this.order.items || this.order.items.length == 0) return 0;
      return this.order.items.reduce((s, x) => (s += x.collectedQuantity || 0), 0);
    },
    sumTotal: function() {
      if (!this.order.items || this.order.items.length == 0) return 0;
      return this.order.items.reduce(
        (s, x) => (s += parseFloat(x.rndPrice || x.product.rndPrice) * parseFloat(x.quantity)),
        0
      );
    }
  },
  methods: {
    loadOrder: function() {
      this.$fb.orderCollection.doc(this.$route.params.id).onSnapshot(snapshot => {
        if (!snapshot.empty) {
          this.order = snapshot.data();
          this.order.items = _.sortBy(this.order.items || [], x => x.dateCreated);
        }
      });
    },
    addNewOrderItem: function() {
      this.selectedOrderItem = {
        id: new Date().getTime(),
        customer: { name: "" },
        product: { name: "" },
        quantity: 1,
        collectedQuantity: 0
      };
      this.$refs.editItemModal.open(this.selectedOrderItem);
    },
    orderItemClicked: function(item) {},
    editOrderItem: function(item) {
      this.selectedOrderItem = { ...item };
      this.selectedOrderItem.oriPrice =
        this.selectedOrderItem.oriPrice || this.selectedOrderItem.product.oriPrice;
      this.selectedOrderItem.rndPrice =
        this.selectedOrderItem.rndPrice || this.selectedOrderItem.product.rndPrice;

      this.$refs.editItemModal.open(this.selectedOrderItem);
    },
    deleteOrderItem: async function(item) {
      var deleteItem = _.find(this.order.items, x => x.id == item.id);
      if (deleteItem) {
        this.$bvModal
          .msgBoxConfirm(
            `Are you sure you want to delete [${deleteItem.quantity} - ${deleteItem.product.name}] of [${deleteItem.customer.name}]?`
          )
          .then(async value => {
            if (value) {
              if (item.distribution) {
                //stock in this item
                var inventoryItem = await this.$fbServices.inventoryService.get(
                  item.distribution.inventoryId
                );
                inventoryItem.quantity += parseFloat(item.quantity);
                await this.$fbServices.inventoryService.set(inventoryItem);
                this.showMessage("Inventory updated!");

                //delete distribution
                await this.$fbServices.distributionService.delete(item.distribution.id);
                this.showMessage("Distribution deleted!");
              }

              //delete order item + update order
              var index = _.findIndex(this.order.items, x => x.id == item.id);
              this.order.items.splice(index, 1);
              this.saveOrder();

              this.selectedOrderItem = undefined;
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    saveOrderItem: async function(orderItem, inventoryItem) {
      if (!orderItem.product) return;
      if (!orderItem.customer) return;

      delete orderItem.customer.nameSearch;
      delete orderItem.product.nameSearch;

      orderItem.quantity = parseFloat(orderItem.quantity);

      this.order.items = this.order.items || [];

      if (inventoryItem) {
        //set distribution information
        var distribution = await this.$fbServices.distributionService.set(
          inventoryItem,
          this.order,
          orderItem
        );
        this.showMessage("Distribution created!");

        //assign distribution information into Order Detail and Inventory Item also
        orderItem.distribution = distribution;
        if (!inventoryItem.distributions) inventoryItem.distributions = [];
        inventoryItem.distributions.push(distribution);

        // deduct remaining product quantity in Inventory
        inventoryItem.quantity -= distribution.quantity;
        await this.$fbServices.inventoryService.set(inventoryItem, distribution);
        this.showMessage("Inventory item quantity updated!");

        // assign product from Inventory into Order detail
        // change
        orderItem.product = inventoryItem.product;
        orderItem.collectedQuantity = orderItem.quantity;
        orderItem.collected = true;
      }

      var index = this.order.items.findIndex(x => x.id == orderItem.id);
      if (index > -1) {
        orderItem.dateModified = new Date();
        this.order.items.splice(index, 1, orderItem);
      } else {
        orderItem.dateCreated = new Date();
        this.order.items.push(orderItem);
      }

      await this.$fb.orderCollection.doc(this.order.id).set(this.order);

      this.selectedOrderItem = undefined;
      this.showMessage("Order saved!");
    },
    calculateTotalOrderItem: function(item) {
      return parseFloat(item.quantity) * parseFloat(item.rndPrice || item.product.rndPrice);
    },
    collectedChanged: function(checked, item) {
      item.collected = checked;
      if (checked) {
        item.collectedQuantity = item.quantity;
      } else {
        item.collectedQuantity = 0;
      }
      var index = this.order.items.findIndex(x => x.id == item.id);

      if (index > -1) {
        item.dateModified = new Date();
        this.order.items.splice(index, 1, item);

        this.saveOrder();
      }
    },
    goToCollectionPage: function() {
      this.$router.push({
        name: "OrderDetailCollection",
        params: { id: this.order.id }
      });
    },
    goToShipmentPage: function() {
      this.$router.push({
        name: "OrderDetailShipment",
        params: { id: this.order.id }
      });
    },
    goToOrderList: function() {
      this.$router.push({
        name: "Orders"
      });
    },
    collectedQuantityChanged: function(item, qty) {
      item.collectedQuantity = qty;
      item.collected = item.collectedQuantity == item.quantity;

      this.saveOrder();
    },
    saveOrder: function() {
      if (saveOrderTimeoutFunc) window.clearTimeout(saveOrderTimeoutFunc);

      saveOrderTimeoutFunc = window.setTimeout(
        () =>
          this.$fb.orderCollection
            .doc(this.order.id)
            .set(this.order)
            .then(() => this.showMessage("Order saved!")),
        1000
      );
    },
    showProductDetailModal: function(item) {
      var productId = item.product.id;
      this.$fb.productCollection
        .doc(productId)
        .get()
        .then(doc => {
          this.product = doc.data();
          this.$refs.productDetail.open(this.product);
        });
    },
    productSaved: function(prod) {}
  }
};
</script>
<style scoped>
.order-detail-mobile-collected-qty {
  width: 120px;
  max-width: 120px;
  float: left;
}

.order-detail-mobile-collected-all {
  padding-top: 5px;
  margin-left: 10px;
  float: left;
}
</style>