<template>
  <div class="animated fadeIn">
    >
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-book"></i>
              Inventory ({{inventoryCount}})
            </span>
            <div class="pull-right">
              <b-link @click="addNewItem" class="ml-1">Add New Item</b-link>
            </div>
          </div>
          <b-row>
            <b-col sm="12" md="6">
              <b-input-group class="mb-5">
                <b-form-input v-model="searchText" @keydown="seartTextKeydown($event)"></b-form-input>

                <b-input-group-append>
                  <span
                    id="searchclear"
                    class="fa fa-close"
                    v-if="searchText != ''"
                    @click="clearSearchText"
                  ></span>
                  <b-button variant="outline-success" @click="search()">
                    <span class="fa fa-search"></span> Search
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
          <view-port>
            <template v-slot:desktop>
              <b-table
                ref="inventoryTable"
                responsive="sm"
                :items="items"
                :fields="fields"
                :small="true"
                selectable
                select-mode="single"
                @row-clicked="itemClicked"
                @row-dblclicked="itemDoubleClicked"
              >
                <template v-slot:cell(index)="data">{{data.index + 1}}</template>
                <template v-slot:cell(name)="data">
                  <div class="prod-thumbnail-name">
                    <div class="thumbnail">
                      <img
                        :src="data.item.product.thumbnail"
                        width="50px"
                        height="50px"
                        v-if="data.item.product.thumbnail"
                      />
                    </div>
                    <div class="name-2">
                      <b-link @click="showProductDetail(data)">{{data.item.product.name}}</b-link>

                      <br />
                      <small>CWPID: {{data.item.product.cwProductId}}</small>
                    </div>
                  </div>
                </template>
                <template v-slot:cell(weight)="data">
                  <strong>{{data.item.product.weight}}(g)</strong>
                  - {{data.item.product.shipPrice | currency(audConfig)}}
                </template>
                <template v-slot:cell(tempPrices)="data">
                  <show-supplier-prices :product="data.item.product" :breakline="true"></show-supplier-prices>
                </template>
                <template v-slot:cell(rndPrice)="data">
                  <span class="price">
                    <strong>{{data.item.product.rndPrice | currency(vndConfig)}}</strong>
                  </span>
                  <br />
                  <small>Sug. Price: {{data.item.product.sugPrice | currency(audConfig)}} - {{data.item.product.sugPrice * 16500 | currency(vndConfig)}}</small>
                </template>
                <template v-slot:cell(dateIn)="data">{{data.item.dateIn.toDate() | date}}</template>
                <template v-slot:cell(quantity)="data">
                  <number-input
                    v-model="data.item.quantity"
                    :min="0"
                    :max="9999"
                    inline
                    controls
                    center
                    size="sm"
                    @change="quantityChange(data.item, $event)"
                  ></number-input>
                </template>
                <template v-slot:cell(action)="data">
                  <b-button
                    type="submit"
                    class="mr-1"
                    size="sm"
                    variant="info"
                    @click="viewHistory(data)"
                  >
                    <i class="fa fa-list"></i> History
                  </b-button>
                  <b-button
                    type="submit"
                    size="sm"
                    variant="success"
                    @click="editItem(data)"
                    class="mr-1"
                  >
                    <i class="fa fa-edit"></i> Edit
                  </b-button>
                  <b-button type="submit" size="sm" variant="danger" @click="deleteItem(data)">
                    <i class="fa fa-remove"></i> Delete
                  </b-button>
                </template>
              </b-table>
            </template>
            <template v-slot:mobile></template>
          </view-port>

          <b-row class="load-more">
            <b-button
              variant="outline-success"
              @click="loadMore($event)"
              v-loading="isLoading"
            >Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
    <inventory-edit-item
      ref="inventoryEditItemModal"
      @item-saved="itemSaved"
      id="inventoryEditItemModal"
    ></inventory-edit-item>
    <product-detail ref="productDetail" :readonly="true" @productSaved="productSaved" />
    <inventory-item-history ref="inventoryItemHistoryModal" id="inventoryItemHistoryModal"></inventory-item-history>
  </div>
</template>

<script>
import fb from "firebase";
import productDetail from "./productDetail";
import { generateSubString } from "@/shared/utils";
import viewPort from "../shared/viewPort";
import InventoryEditItem from "./InventoryEditItem";
import ShowSupplierPrices from "./ShowSupplierPrices";
import numberInput from "../shared/numberInput";
import InventoryItemHistory from "./InventoryItemHistory";
var debounceQuantitySaveFunc;
export default {
  name: "Inventory",
  components: {
    productDetail,
    viewPort,
    InventoryEditItem,
    ShowSupplierPrices,
    numberInput,
    InventoryItemHistory
  },
  data() {
    return {
      items: [],
      searchText: "",
      pageSize: 10,
      lastVisible: undefined,
      isLoading: false,
      selectedItemIndex: -1,
      inventoryCount: 0,
      fields: [
        { key: "index", label: "#", class: "i-index" },
        { key: "name", class: "i-name" },
        { key: "weight", label: "Weight (g)", class: "i-weight" },
        { key: "tempPrices", label: "Temp. Prices", class: "i-temp-price" },
        { key: "rndPrice", label: "Price", class: "i-rnd-price" },
        { key: "dateIn", label: "Date In", class: "i-date-in" },

        { key: "quantity", label: "Quantity", class: "i-quantity" },
        { key: "note", label: "Note", class: "i-note" },
        { key: "action", label: "Action", class: "i-action" }
      ]
    };
  },
  mounted: function() {
    var self = this;
    this.loadData();
    this.$fb.counterService.getProductCount().then(count => (this.productCount = count));
  },
  watch: {
    searchText: function(newValue, oldValue) {
      if (oldValue != "" && newValue == "") {
        this.products = [];
        this.loadProducts();
      }
    }
  },
  computed: {},
  methods: {
    loadData: function() {
      this.isLoading = true;
      var q = this.$fb.inventoryCollection
        .orderBy("product.name")
        .orderBy("dateIn")
        .limit(10);
      if (this.lastVisible) q = q.startAfter(this.lastVisible);
      q = q
        .get()
        .then(ss => {
          this.isLoading = false;
          if (ss.empty) return [];
          this.lastVisible = _.last(ss.docs);
          return ss.docs.map(d => d.data());
        })
        .then(data => {
          console.log(data);
          data.forEach(d => this.items.push(d));
        });
    },
    addNewItem: function() {
      this.$refs.inventoryEditItemModal.open();
    },
    loadMore: function() {
      this.loadData();
    },
    search: function() {},
    seartTextKeydown: function() {},
    clearSearchText: function() {},
    itemClicked: function() {},
    itemDoubleClicked: function(item, index) {
      this.selectedItemIndex = index;
      this.$refs.inventoryEditItemModal.open(item);
    },
    quantityChange: function(item, number) {
      if (item.id) {
        item.quantity = number;
        this.saveItem(item);
      }
    },
    saveItem: function(item) {
      if (debounceQuantitySaveFunc) window.clearTimeout(debounceQuantitySaveFunc);
      debounceQuantitySaveFunc = window.setTimeout(() => {
        if (item.id) {
          this.$fbServices.inventoryService
            .set(item)
            .then(() => this.showMessage("Inventory item saved!"));
        }
      }, 500);
    },
    editItem: function(data) {
      this.selectedItemIndex = data.index;
      this.$refs.inventoryEditItemModal.open(data.item);
    },
    deleteItem: function(data) {
      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete this item?`)
        .then(value => {
          if (value) {
            this.$fb.inventoryCollection
              .doc(data.item.id)
              .delete()
              .then(() => {
                var index = this.items.findIndex(x => x.id == data.item.id);
                if (index > -1) this.items.splice(index, 1);
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    itemSaved: function(item) {
      if (this.selectedItemIndex > -1) {
        this.items.splice(this.selectedItemIndex, 1, item);
        this.selectedItemIndex = -1;
      } else {
        this.items.push(item);
      }
    },
    showProductDetail: function(data) {
      var { index, item } = data;
      this.selectedItemIndex = index;
      this.$refs.productDetail.open(item.product);
    },
    productSaved: function(prod) {
      if (this.selectedItemIndex > -1) {
        var item = this.items[this.selectedItemIndex];
        item.product = prod;
        this.items.splice(this.selectedItemIndex, 1, item);
        this.saveItem(item);
        this.selectedItemIndex = -1;
      }
    },
    viewHistory: function(data) {
      this.$refs.inventoryItemHistoryModal.open(data.item);
    }
  }
};
</script>
<style>
#searchclear {
  position: absolute;
  right: 90px;
  top: 0;
  bottom: 0;
  height: 14px;
  margin: auto;
  font-size: 14px;
  cursor: pointer;
  color: #ccc;
  z-index: 1019;
}
.i-index {
  width: 2%;
}
.i-name {
  width: 20%;
}
.i-weight {
  width: 8%;
}
.i-temp-price {
  width: 12%;
}
.i-rnd-price {
  width: 13%;
}
.i-date-in {
  width: 7%;
}
.i-quantity {
  width: 10%;
  text-align: center;
  padding-left: 10px !important;
  padding-right: 10px !important;
}
.i-note {
  width: 10%;
}
.i-action {
  text-align: right;
}
</style>
