<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-book"></i>
              Products ({{productCount}})
              <b-link class="pull-right" @click="forceCheckPriceAgain">CW Check Price</b-link>
            </span>
          </div>
          <div v-if="this.showCheckPriceProgress" class="mb-5">
            <p>There are {{this.productsNotCheckedPrice.length}} products have been out-of-dated, click button 'Start Check Price' to get new price from Chemiswarehourse</p>
            <b-progress
              :value="this.currentProductCheckingIndex"
              :max="this.productsNotCheckedPrice.length"
              variant="info"
              height="2rem"
              animated
              show-value
            >
              <b-progress-bar :value="this.currentProductCheckingIndex">
                Progress:
                <strong>{{ this.currentProductCheckingIndex }} / {{ this.productsNotCheckedPrice.length }}</strong>
              </b-progress-bar>
            </b-progress>

            <p>#{{this.currentProductChecking.cwProductId}} - {{this.currentProductChecking.name}}</p>

            <b-button type="submit" size="sm" variant="success" @click="startCheckPrice()">
              <i class="fa fa-line-chart"></i> Start Check Price
            </b-button>
          </div>

          <b-form-group label="Show only increase/decrease">
            <b-form-checkbox
              v-model="filter.showOnlyDecrease"
              @change="showOnlyDecreaseChange"
              inline
            >Show Only Decrease</b-form-checkbox>
            <b-form-checkbox
              v-model="filter.showOnlyIncrease"
              @change="showOnlyIncreaseChange"
              inline
            >Show Only Increase</b-form-checkbox>
          </b-form-group>

          <view-port>
            <template v-slot:desktop>
              <div>
                <b-table
                  ref="trackingProduct"
                  responsive="sm"
                  :items="products"
                  :fields="fields"
                  :small="true"
                  class="tracking"
                  selectable
                  select-mode="single"
                >
                  <template v-slot:cell(index)="data">{{data.index + 1}}</template>
                  <template v-slot:cell(name)="data">
                    <div class="prod-thumbnail-name">
                      <div class="thumbnail">
                        <img
                          :src="data.item.thumbnail"
                          width="50px"
                          height="50px"
                          v-if="data.item.thumbnail"
                        />
                      </div>
                      <div class="name-2">
                        {{data.value}}
                        <br />
                        <small>PID: {{data.item.cwProductId}}</small>
                      </div>
                    </div>
                  </template>
                  <template v-slot:cell(price)="data">
                    <del>{{data.item.cwTracking.oldPrice | currency(audConfig)}}</del>
                    &nbsp;
                    <span
                      style="font-size: 18px; font-weight: bold"
                    >{{data.item.cwTracking.price | currency(audConfig)}}</span>
                  </template>
                  <template v-slot:cell(change)="data">
                    <i
                      class="fa fa-angle-double-up fa-lg"
                      aria-hidden="true"
                      v-if="data.item.cwTracking.priceStatus == 'increase'"
                      style="color: red; font-size: 24px"
                    ></i>
                    <i
                      class="fa fa-angle-double-down fa-lg"
                      aria-hidden="true"
                      v-if="data.item.cwTracking.priceStatus == 'decrease'"
                      style="color: green; font-size: 24px"
                    ></i>
                    ({{data.item.cwTracking.percentage}}%)
                  </template>
                  <template
                    v-slot:cell(lastChecked)="data"
                  >{{data.item.cwTracking.lastChecked.toDate() | date}}</template>
                  <template v-slot:cell(action)="data">
                    <b-button
                      type="submit"
                      size="sm"
                      variant="success"
                      @click="editProduct(data)"
                      class="mr-1"
                    >
                      <i class="fa fa-edit"></i> Edit
                    </b-button>
                  </template>
                </b-table>
              </div>
            </template>
            <template v-slot:mobile>
              <b-table
                ref="trackingProduct"
                responsive="sm"
                :items="products"
                :fields="mobileFields"
                :small="true"
                class="tracking"
                selectable
                select-mode="single"
              >
                <template v-slot:cell(index)="data">
                  <div class="row">
                    <div class="col-4" style="overflow: hidden">
                      <img
                        :src="data.item.thumbnail"
                        width="150px"
                        height="150px"
                        style="margin-left: -30px"
                      />
                    </div>
                    <div class="col-8">
                      <strong>{{data.item.name}}</strong>
                      <br />
                      <del>{{data.item.cwTracking.oldPrice | currency(audConfig)}}</del>
                      <span
                        class="ml-1"
                        style="font-size: 18px; font-weight: bold"
                      >{{data.item.cwTracking.price | currency(audConfig)}}</span>
                      <i
                        class="fa fa-angle-double-up fa-lg ml-1"
                        aria-hidden="true"
                        v-if="data.item.cwTracking.priceStatus == 'increase'"
                        style="color: red; font-size: 24px"
                      ></i>
                      <i
                        class="fa fa-angle-double-down fa-lg"
                        aria-hidden="true"
                        v-if="data.item.cwTracking.priceStatus == 'decrease'"
                        style="color: green; font-size: 24px"
                      ></i>
                      ({{data.item.cwTracking.percentage}}%)
                      <br />
                      Last checked: {{data.item.cwTracking.lastChecked.toDate() | date}}
                      <br />
                      <b-button
                        type="submit"
                        size="sm"
                        variant="success"
                        @click="editProduct(data)"
                        class="mt-1"
                      >
                        <i class="fa fa-edit"></i> Edit
                      </b-button>
                    </div>
                  </div>
                </template>
              </b-table>
            </template>
          </view-port>

          <b-row class="load-more">
            <b-button
              variant="outline-success"
              @click="loadMoreProduct($event)"
              v-loading="isLoadingMore"
            >Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->

    <product-detail
      ref="productDetail"
      id="productEdit"
      :item="product"
      @productSaved="productSaved"
    />
  </div>
</template>

<script>
import productDetail from "./productDetail";
import viewPort from "../shared/viewPort";

export default {
  name: "Products",
  components: {
    productDetail,
    viewPort
  },
  data() {
    return {
      productsNotCheckedPrice: [],
      currentProductCheckingIndex: 0,
      currentProductChecking: {},
      products: [],
      product: {},
      productBatches: "",
      useMobileView: false,
      searchText: "",
      pageSize: 10,
      lastVisible: undefined,
      productCount: 0,
      isLoadingMore: false,
      showCheckPriceProgress: false,
      fields: [
        { key: "index", label: "#", class: "index" },
        { key: "name", class: "name", class: "name" },
        { key: "price", label: "Price", class: "price text-right" },
        { key: "change", label: "", class: "change text-left" },
        { key: "lastChecked", label: "Last Checked", class: "text-center last-checked" },
        { key: "action", label: "Action", class: "action text-right" }
      ],
      mobileFields: [{ key: "index", label: "" }],
      filter: { showOnlyDecrease: true, showOnlyIncrease: false }
    };
  },
  mounted: function() {
    var self = this;
    self.useMobileView = window.innerWidth < 700;
    window.addEventListener("resize", function() {
      self.useMobileView = window.innerWidth < 700;
    });

    this.getOutDateProducts();
    this.getCheckedProducts(null);
    this.$fb.counterService.getProductCount().then(count => (this.productCount = count));
  },
  watch: {},
  computed: {},
  methods: {
    getCheckedProducts: function(lastVisible) {
      var q = this.$fb.productCollection
        .where("cwTracking.percentage", ">", 0)
        .orderBy("cwTracking.percentage", "desc");

      if (this.filter.showOnlyDecrease) {
        q = q.where("cwTracking.priceStatus", "==", "decrease");
      } else if (this.filter.showOnlyIncrease) {
        q = q.where("cwTracking.priceStatus", "==", "increase");
      }

      if (lastVisible) q = q.startAfter(lastVisible || 0);
      return q
        .limit(20)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
            return this.products;
          }
        });
    },
    loadMoreProduct: function(e) {
      this.isLoadingMore = true;
      this.getCheckedProducts(this.lastVisible).then(() => (this.isLoadingMore = false));
    },
    cwCheckPrice: function() {
      this.$fb.productCollection
        .where("cwProductId", ">", "0")
        .orderBy("cwProductId")
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            return _.map(snapshot.docs, doc => doc.data());
          } else return [];
        })
        .then(products => {});
    },
    getOutDateProducts: function(forceCheckAll) {
      var today = new Date().setMin();

      return this.loadCwProducts()
        .then(products => {
          return forceCheckAll
            ? products
            : _.filter(
                products,
                item =>
                  !item.cwTracking ||
                  !item.cwTracking.lastChecked ||
                  item.cwTracking.lastChecked.toDate() < today
              );
        })
        .then(products => {
          this.showCheckPriceProgress = products.length > 0;
          this.productsNotCheckedPrice = products;
        });
    },
    startCheckPrice: function() {
      this.currentProductCheckingIndex = 0;
      if (this.currentProductCheckingIndex < this.productsNotCheckedPrice.length)
        this.doCheckPrice();
    },
    doCheckPrice: function() {
      this.currentProductChecking = this.productsNotCheckedPrice[this.currentProductCheckingIndex];
      var term = this.currentProductChecking.cwProductId;
      return this.$http.get(this.appSettings.cwCheckPriceApi.replace("{term}", term)).then(res => {
        if (res && res.status == 200 && res.data) {
          var data = res.data;
          var validData = _.find(data, item => parseFloat(item.id) > 0);
          if (validData && validData.id == term) {
            var trackingData = this.decorateData(validData, this.currentProductChecking);
            console.log(trackingData);
            this.currentProductChecking.cwTracking = trackingData;
            this.$fb.productCollection
              .doc(this.currentProductChecking.id)
              .set(this.currentProductChecking);
          }
        }
        this.currentProductCheckingIndex++;
        if (this.currentProductCheckingIndex < this.productsNotCheckedPrice.length)
          window.setTimeout(this.doCheckPrice, 200);
        else {
          this.getCheckedProducts();
        }
      });
    },
    decorateData: function(item, currentProduct) {
      item.price = parseFloat(item.Price);

      var cwsp = currentProduct.supplierPrices.find(x => x.supplier == "CW");
      var cwPrice = parseFloat((cwsp && cwsp.price) || 0);
      item.oldPrice = parseFloat(cwPrice);

      item.lastChecked = new Date();
      var price = item.price;

      if (price > cwPrice + 0.5) {
        item.priceStatus = "increase";
        item.percentage = (((price - cwPrice) / cwPrice) * 100).toFixed(0);
      } else if (price < cwPrice - 0.5) {
        item.priceStatus = "decrease";
        item.percentage = (((cwPrice - price) / cwPrice) * 100).toFixed(0);
      } else {
        item.priceStatus = "same";
        item.percentage = 0;
      }

      return {
        price: parseFloat(item.price),
        oldPrice: parseFloat(item.oldPrice),
        lastChecked: item.lastChecked,
        priceStatus: item.priceStatus,
        percentage: parseFloat(item.percentage)
      };
    },
    loadCwProducts: function() {
      return this.$fb.productCollection
        .where("cwProductId", ">", "0")
        .orderBy("cwProductId")
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            return _.map(snapshot.docs, doc => doc.data());
          } else return [];
        });
    },
    editProduct: function(data) {
      var { index, item } = data;
      this.$refs.productDetail.open({ ...item });
    },
    productSaved: function() {},
    forceCheckPriceAgain: function() {
      this.getOutDateProducts(true);
    },
    filterCheckedProducts: function() {
      this.$nextTick(() => {
        this.products = [];
        this.getCheckedProducts();
      });
    },
    showOnlyDecreaseChange: function(checked) {
      if (checked) this.filter.showOnlyIncrease = false;
      this.filterCheckedProducts();
    },
    showOnlyIncreaseChange: function(checked) {
      if (checked) this.filter.showOnlyDecrease = false;
      this.filterCheckedProducts();
    }
  }
};
</script>
<style>
.tracking th.index {
  width: 1%;
}

.tracking th.price {
  width: 15%;
}
.tracking th.change {
  width: 5%;
}

.tracking th.last-checked {
  width: 10%;
}

.tracking th.action {
  width: 5%;
}

.prod-thumbnail-name {
  display: flex;
  justify-content: flex-start;
}
.prod-thumbnail-name .name-2 {
  margin-left: 5px;
}
.prod-thumbnail-name .thumbnail {
  width: 50px;
  min-width: 50px;
  height: 50px;
  min-height: 50px;
}
</style>
